import React from 'react';
import '../../styles/styles.css'

function 전자전달() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default 전자전달;

function MainContents() {
	const title = 'Electron Transfer in Photocatalysis';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>
			<h3>1. 개요</h3>
			<br/><img src="../images/photocatalyst/electrontransfer_8.jpg" width="600" alt=''/><br/>
			<ul>
				<li>반도체 광촉매는 물과반응하여 수소 및 산소를 발생하는 반응과 유기물질과 반응 하는 경우로 크게 나눌 수 있다.</li>
				<li>물과 반응할 경우
					<ul>
						<li>물은 여기된 전자 두개를 받아들여 수소로 분리되고</li>
						<li>물은 전공 4 개와 반응하여 전자 4개를 빼았기고 산소를 발생한다. </li>
						<li>물의 분해(water split)는 유기물과의 반응과 비교하여 많은 전자 정공이 소비된어야 한다.</li>
						<li>따라서, 반응이 어렵다.</li>
					</ul>
				</li>
				<li>유기물질과 반응할 경우
					<ul>
						<li>물 분해와는 다르게 유기물은 전자 공여체로 반응 되기 수비다.</li>
						<li>유기물은 일반적으로 물보다 더 강한 환원제이므로 유기물을 산화하는 데는 물과 비교하여 정공이 덜 필요하다.</li>
					</ul>
				</li>
				<li>TiO2 는 Ti<sup>4+</sup> 원자와 6개의 O<sup>2-</sup>가 함께 배위되어 TiO6 팔면체를 형성하는 전이금속 산화물 반도체이다.</li>
				<li>대기압하의 산소 분력에서 주요 결함으로 산소 결손(Ov)을 갖는 비화학양론적이어서 고유한 n형 반도체의 특성을 갖는다.</li>
				<li>n형 TiO2 표면의 산소 결손(Ov)은 CB에서 여분의 짝을 이루지 않은 전자로 나타나며 이는 공여체와 유사한 상태로 작용한다. </li>
				<li>이는 표면에 축적층을 생성하여 하향 밴드 굽힘을 초래합니다</li>
				<li>TiO2 의 광촉매 활성은 전하 운반체 역학과 밀접한 관련이 있다. </li>
				<li>e-/h+ 쌍은 조사 후 몇 펨토초 이내에 생성되며 벌크나 표면에서 쉽게 재결합할 수 있다.</li>
				<li>그러나 다른 전하 운반체는 재결합을 피해 표면으로 이동하여 산화환원 반응에서 계면 전하 이동이 일어나기 전에 갇힐 수 있습니다</li>
			</ul>

			<hr className='class-1'/>
			<h3>2. 광생성 전하 캐리어의 반응</h3>
						
			<table className='table  table-bordered'>
				<tbody>
					<tr className='table-warning'>
						<td rowspan='6'><br/><img src="../images/photocatalyst/electrontransfer_7.jpg" width="600" alt=''/><br/></td>
						<td>(1)</td>
						<td>Eg 이상의 광에너지 흡수에 의한 여기</td>
					</tr>
					<tr className='table-info'>
						<td>(2)</td>
						<td>전자 정공의 쌍 분리</td>
					</tr>
					<tr className='table-warning'>
						<td>(3)</td>
						<td>e-는 촉매의 표면을 향한 확산되고, 촉매 표면에 흡착된 전자 수용체(A)를 환원시킨다.</td>
					</tr>
					<tr className='table-info'>
						<td>(4)</td>
						<td>h+는 촉매의 표면을 향한 확산되고, 촉매 표면에 흡착된 전자 공여체(D)를 산화시킨다.</td>
					</tr>
					<tr className='table-warning'>
						<td>(5)</td>
						<td>광생성 전하 캐리어(e-/h+)가 표면으로 확산되어 전자수용체(A) 및 전자 공여체(D)와 반응하지 못하고 표면에서 재결합</td>
					</tr>
					<tr className='table-info'>
						<td>(6)</td>
						<td>광생성 전하 캐리어가 표면으로 확산되지 못하고 벌크 내에서 재결합</td>
					</tr>
				</tbody>
			</table>
			
			<table className='table  table-bordered'>
					<tbody>
					<tr className='table-primary'>
						<th></th>
						<th>Step</th>
						<th>explain</th>
						<th>reaction</th>
						<th></th>
					</tr>
					<tr className='table-warning'>
						<th rowspan='8'><br/><img src="../images/photocatalyst/electrontransfer_6.jpg" width="500" alt=''/><br/></th>
						<th>Step 1</th>
						<td>Electron-hole pair generation</td>
						<td>TiO<sub>2</sub> + hν → TiO2 (e<sup>-</sup><sub>CB</sub> + h<sup>+</sup><sub>VB</sub>)</td>
						<td>shallow trap site</td>
					</tr>
					<tr className='table-info'>
						<th>Step 2</th>
						<td>Trapping CB electrons ( e<sup>-</sup><sub>CB</sub> ) 
						at defect Ti<sup>4+</sup> sites</td>
						<td>Ti<sup>4+</sup><sub>ds</sub> + e<sup>-</sup><sub>CB</sub> → Ti<sup>3+</sup><sub>ds</sub></td>
						<td>deep trapping</td>
					</tr>
					<tr className='table-warning'>
						<th>Step 3</th>
						<td>Trapping VB holes(h<sup>+</sup><sub>VB</sub>) <br/>
						at terminal Ti—OH or surface Ti—O—Ti sites</td>
						<td>Ti—O<sub>s</sub>H + h<sup>+</sup><sub>VB</sub> → Ti—O<sub>s</sub>H<sup>・+</sup> <br/>
								Ti—Os—Ti + h<sup>+</sup><sub>VB</sub> → Ti—O<sub>s</sub><sup>・+</sup>-Ti</td>
						<td></td>
					</tr>
					<tr className='table-info'>
						<th>Step 4</th>
						<td>Reduction of adsorbed electron acceptor(A<sub>ad</sub>) with e<sup>-</sup><sub>CB</sub> <br/>
						 at reduction sites</td>
						<td>e<sup>-</sup><sub>CB</sub> + A<sub>ad</sub> → A<sup>・−</sup><sub>ad</sub></td>
						<td></td>
					</tr>
					<tr className='table-warning'>
						<th>Step 5</th>
						<td>Reduction of A<sub>ad</sub> with electrons trapped <br/>
						at defect sites (Ti<sup>3+</sup><sub>ds</sub>)</td>
						<td>Ti<sup>3+</sup><sub>ds</sub> + A<sub>ad</sub> → Ti<sup>4+</sup><sub>ds</sub> + A<sup>・−</sup><sub>ad</sub></td>
						<td></td>
					</tr>
					<tr className='table-info'>
						<th>Step 6</th>
						<td>Oxidation of adsorbed electron donor(D<sub>ad</sub>) by trapped holes <br/>
							at oxidation sites</td>
						<td>Ti—O<sub>s</sub>H<sup>・+</sup> + D<sub>ad</sub> → Ti—O<sub>s</sub>H <br/>
								Ti—O<sub>s</sub>H—Ti + D<sub>ad</sub> → Ti—O<sub>s</sub>—Ti + D<sup>・+</sup><sub>ad</sub></td>
						<td>shallow trapping</td>
					</tr>
					<tr className='table-warning'>
						<th>Step 7</th>
						<td>Recombination of e<sup>-</sup><sub>CB</sub> with trapped holes</td>
						<td>e<sup>-</sup><sub>CB</sub> + Ti—O<sub>s</sub>H<sup>・+</sup> → Ti—O<sub>s</sub>H <bs/>
								e<sup>-</sup><sub>CB</sub> + Ti—O<sub>s</sub><sup>・+</sup>-Ti → Ti—O<sub>s</sub>—Ti</td>
						<td></td>
					</tr>
					<tr className='table-info'>
						<th>Step 8</th>
						<td>Recombination of Ti<sup>3+</sup><sub>ds</sub> with trapped holes</td>
						<td>Ti<sup>3+</sup><sub>ds</sub> + Ti—O<sub>s</sub>H<sup>・+</sup> → Ti<sup>4+</sup><sub>dt</sub> <br/>
								Ti<sup>3+</sup><sub>ds</sub> + Ti—O<sub>s</sub><sup>・+</sup>—Ti → Ti<sup>4+</sup><sub>dt</sub> + Ti—O<sub>s</sub>—Ti</td>
						<td></td>
					</tr>
				</tbody>

			</table>





			<br/><img src="../images/photocatalyst/electrontransfer_4.jpg" width="800" alt=''/><br/>
			<ul>
				<li>광생성 전하 캐리어는 대량으로 또는 표면에 갇힐 수 있습니다. </li>
				<li>일반적으로 반도체 나노입자 에서는 표면 아래 또는 표면 영역에서 표면 트랩핑이 선호됩니다.</li> 
				<li>갇힌 정공, 갇힌 전자 및 자유 전자를 포함한 시간적 종은 트래핑 단계에서 전하 캐리어의 주요 상태입니다. </li>
				<li>Yoshihara et al.은 400~2500 nm의 넓은 파장 범위에서 TAS를 사용합니다.</li>
				<li>갇힌 정공과 전자는 입자 표면에 국한되어 있는 반면 자유 전자는 벌크에 분포되어 있음을 발견했습니다.</li>

				<li>트래핑은 TiO 2 결정상에 따라 중요합니다. </li>
				<li>anatase은 공여체-수용체 재조합으로 인해 가시적인 방출을 보이는 것으로 밝혀졌으며, </li>
				<li>여기서 산소 결손 및 수산기 그룹은 주로 공여체 및 수용자 사이트 역할을 합니다. </li>
				<li>rutile은 갇힌 전자와 자유 정공의 재결합으로 인해 강한 NIR 발광을 보이는 
				반면, 갇힌 전자는 직접 트래핑 또는 트랩 간 호핑이라는 두 가지 경로를 통해 형성되었습니다. </li>
				<li>광생성 캐리어의 태핑 및 재결합에 대한 개략도가 위 그림에 나와 있습니다 .</li>
			</ul>

			<hr className='class-1'/>
			<h3>2. 전하 캐리어 trapping, recombination</h3>
			<br/><img src="../images/photocatalyst/electrontransfer_3.jpg" width="400" alt=''/><br/>
			<br/><img src="../images/photocatalyst/electrontransfer_2.jpg" width="400" alt=''/><br/>
			<ul>
				<li>광자 에너지가 밴드 갭보다 크면 밴드 간 전이가 지배적이며, fs 시간 척도에서 원자가 밴드에서 전도 밴드로 전자를 여기시킵니다(식 1)</li>
				<li>전자와 정공이 광촉매 표면으로 무작위로 이동하여 ps 이하의 시간 단위로 거기에 갇혀 있다고 제안했습니다(식 2a).</li>
				<li>또한 벌크 포획 사이트(식 2b, 2c)에 포획될 수 있습니다.</li>
				<li>전자가 에너지적으로 동일하고 아마도 전도대 가장자리에 매우 가깝기 때문에 평형 상태에 있는 표면과 벌크 트랩 사이에서 이동할 수 있다고 제안했습니다.</li>
				<li><br/><img src="../images/photocatalyst/electrontransfer_1.jpg" width="400" alt=''/><br/>
				표면 relaxation 역학에서 제안된 표면에 갇힌 전자와 벌크 전자의 에너지 준위를 보여줍니다.
				</li>
				<li>광촉매 표면에서 포획된 전자와 정공은 표면에서 재결합하거나(식 3) 각각 수용체(A) 또는 공여체(D) 분자로 전달되어 이들과 반응할 수 있습니다(식 4).</li>
				<li>환원된 수용체와 정공 또는 산화된 공여체와 전자 사이의 전하 이동 후 반응은 표면에 강하게 흡착될 때 발생할 수 있습니다</li>
				<li>위의 메커니즘에 따르면 계면 전하 이동 효율은 두 가지 중요한 프로세스, 즉 전하 재결합과 트래핑(포획 상태)간의 경쟁에 이어 포획된 캐리어 재결합과 계면 전하 이동 간의 경쟁에 의해 제한됩니다.</li>
			</ul>


			<hr className='class-1'/>
			<h3>3. 반도체 입자의 갇힌 대 자유 전하 캐리어</h3>
			<ul>
				<li>전도대와 포획 상태 사이의 전하 캐리어 분포는 중요한 현상입니다.</li>
				<li>이종 광화학 전하 이동 반응은 벌크 및 표면에 트랩핑 사이트를 포함하는 반도체 나노입자를 사용하여 수행되는 경우가 많습니다.</li>
				<li>정공 제거제(스케빈저)가 없으면 산소(즉, 전자 제거제)에서 전하를 분리 시켜서, 정공이 물을 산화시켜 흡착된 하이드록실 라디칼을 생성 한다.</li>
			</ul>


			<hr className='class-1'/>
			<h3>4. 전하 이동에 대한 시공간 이미징</h3>
			<ul>
				<li>광촉매에서 촉매 표면 부위로의 광 유도 전하 이동은 광촉매 효율에 영향을 미치는 중요한 인자이다.</li>
				<li>그러나 나노미터에서 마이크로미터, 펨토초에서 초까지 분해하고, 넓은 공간 범위에 걸쳐서 측정하는 것은 어렵습니다 .</li>
				<li>정상 상태 전하 분포는 현미경 기술러는 광촉매에서 시공간적으로 진화하는 전하 이동 프로세스 광촉매 입자는 추적할 수 없다.</li>
			</ul>
			<br/><img src="../images/photocatalyst/electrontransfer_5.jpg" width="400" alt=''/><br/>
			

			<hr className='class-1'/>
			<h3>참고문헌</h3>
			<ol>
				<li><a href='https://www.mdpi.com/2073-4344/7/10/303'>Reactivity of Trapped and Accumulated Electrons in Titanium Dioxide Photocatalysis</a></li>
				<li><a href='https://www.sciencedirect.com/science/article/pii/S0920586118315177#bib0385'>Charge carrier trapping, recombination and transfer</a></li>
				<li><a href='https://pubs.acs.org/doi/10.1021/jp031305d?src=getftr'>Identification of Reactive Species in Photoexcited Nanocrystalline TiO2 Films by Wide-Wavelength-Range (400−2500 nm) Transient Absorption Spectroscopy</a></li>
				<li><a href='https://www.mdpi.com/2073-4344/11/3/317'>방향족 수질 오염물질을 연료로 전환하기 위한 TiO2 광촉매</a></li>
				<li><a href='https://pubs.acs.org/doi/10.1021/cr1002326'>전자정공 반응 이론 및 해석방법 잘 되어 있슴</a></li>
				<li><a href=''></a></li>
				<li><a href=''></a></li>
				<li><a href=''></a></li>
				<li><a href=''></a></li>
			</ol>
			
		</div>
	)
}
