import React from 'react';
import '../../styles/styles.css'

function MO다이어그램() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default MO다이어그램;

function MainContents() {
	const title = 'Molecular orbital diagram';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>
			<h3>반도체-광촉매의 전자 구조</h3>
			<br/><img src="../images/photocatalyst/MO다이어그램_2.jpg" width="600" alt=''/><br/>
			<ul>
				<li>전자 궤도는 병합되어 두 개의 띠, 즉 가전자대(VB)와 전도대(CB)로 분리됩니다. </li>
				<li>반도체의 VB와 CB는 각각 HOMO(최고 점유 분자 궤도)와 LUMO(최저 비점유 분자 궤도)로 구성됩니다</li>
				<li>온도 0K에서 VB는 전자로 완전히 채워진 낮은 밴드이고, CB는 비어 있는 높은 밴드입니다</li>
				<li>반도체의 밴드 구조를 형성하는 전자 오비탈 간의 상호 작용이 위 그림에 나와 있습니다 . </li>
				<li>반도체의 Eg 값 은 VB에서 CB로의 전자 촉진이 해당 물질로의 에너지 전달을 통해 시작될 수 있을 만큼 충분히 작습니다</li>
				<li>Eg 보다 높거나 같은 빛 에너지는 VB에서 전자의 여기를 유도하여 CB에서 부분적으로 채워진 상태를 차지하도록 유도하여 양전하 정공으로 알려진 VB에 전자 공극을 생성합니다</li>
				<li>정공은 물질에서 전자가 빠저나간 빈 공간이라 생각헐 수 있으므로 다른 전자에 의해 채워질 수 있기 때문에 이동성 개체로 생각할 수 있다. </li>
				<li>마찬가지로 CB의 전자는 정공의 전자보다 더 높은 이동성을 갖는 이동성 개체입니다</li>
			</ul>


			<hr className='class-1'/>
			<h3>Molecular orbital diagram</h3>
			<ul>
				<li>원자가 결합하여 분자를 만들어지면, 원자의 전자수의 원자 궤도가 결합하여 동일한 수의 분자 궤도를 형성한다는 것입니다 .</li>
				<li>Molecular orbital diagram을 알아야 분자의 물리적 성절에 대해 설명할 수 있다.</li>
				<li>H2O의 Molecular orbital diagram
				<br/><img src="../images/photocatalyst/MO다이어그램_1.jpg" width="900" alt=''/><br/>
				</li>
			</ul>

			<hr className='class-1'/>
			<h3>참고문헌</h3>
			<ol>
				<li><a href='https://en.wikipedia.org/wiki/Molecular_orbital_diagram'>wikipedia, Molecular orbital diagram</a></li>
				<li><a href='https://www.mdpi.com/2073-4344/11/3/317'>방향족 수질 오염물질을 연료로 전환하기 위한 TiO2 광촉매</a></li>
				<li><a href=''></a></li>
				<li><a href=''></a></li>
			</ol>
			
		</div>
	)
}
